<template>
   <div class ="BxSummaryStudent">
      <div class="BxSummaryStudent__BXcontent">
         <div v-if="!myData">
            <content-loader
               :animate="true"
               :height="500"
               :width="350"
               :speed="2"
               primaryColor="#f3f3f3"
               secondaryColor="#ecebeb"
            >
               
               <rect x="80" y="20" rx="0" ry="0" width="200" height="30" />
               <rect x="130" y="60" rx="0" ry="0" width="100" height="20" />
               <rect x="25" y="120" rx="0" ry="0" width="140" height="25" />
               <rect x="180" y="120" rx="0" ry="0" width="140" height="25" />
               <rect x="25" y="160" rx="0" ry="0" width="140" height="25" />
               <rect x="180" y="160" rx="0" ry="0" width="140" height="25" />
               <rect x="25" y="200" rx="0" ry="0" width="140" height="25" />
               <rect x="180" y="200" rx="0" ry="0" width="140" height="25" />
               <rect x="25" y="240" rx="0" ry="0" width="140" height="25" />
               <rect x="180" y="240" rx="0" ry="0" width="140" height="25" />
               <rect x="25" y="280" rx="0" ry="0" width="140" height="25" />
               <rect x="180" y="280" rx="0" ry="0" width="140" height="25" />
               <rect x="80" y="340" rx="3" ry="3" width="190" height="80" />
               <rect x="130" y="440" rx="1" ry="1" width="100" height="30" />
               
               

            </content-loader>
         </div>
         <div class="BxSummaryStudent__BXcontent__Bxheader" v-if="myData">
            <p>Sessions Summaries</p>
            <p>Your Live Session has Ended</p>
         </div>
         <div class="BxSummaryStudent__BXcontent__BxForm" v-if="myData">
            <div class="BxValue">
               <div class="bxlabel">
                  <p>Student Name</p>
                  <span>:</span>
               </div>
               <div class="BxAns">
                  <p>{{StudentName}}</p>
               </div>
            </div>
            <div class="BxValue">
               <div class="bxlabel">
                  <p>Date</p>
                  <span>:</span>
               </div>
               <div class="BxAns">
                  <p>{{DateSession | dateFormat}}</p>
               </div>
            </div>
            <div class="BxValue">
               <div class="bxlabel">
                  <p>Start Time</p>
                  <span>:</span>
               </div>
               <div class="BxAns">
                   <p>{{StartTimes}}</p>
               </div>
            </div>
            <div class="BxValue">
               <div class="bxlabel">
                  <p>End Time</p>
                  <span>:</span>
               </div>
               <div class="BxAns">
                  <p>{{EndTimes}}</p>
               </div>
            </div>
            <div class="BxValue">
               <div class="bxlabel">
                  <p>Student Joined at</p>
                  <span>:</span>
               </div>
               <div class="BxAns">
                  <p>{{StudentJoin}}</p>
               </div>
            </div>
            <div class="BxValue">
               <div class="bxlabel">
                  <p>Coach Joined at</p>
                  <span>:</span>
               </div>
               <div class="BxAns">
                  <p>{{CoachJoin}}</p>
               </div>
            </div>
            <div class="BxNote">
               <p>Coach Notes</p>
               <v-textarea
                  outline
                  v-model="CoachNote"
               ></v-textarea>
            </div>
         </div>
         <div class="BxSummaryStudent__BXcontent__BxFooter" v-if="myData">
            <v-btn class="btnSqure" :disabled="CoachNote == '' " @click="PostCoachSummary" >Confirm</v-btn>
         </div>
      </div>
      <v-dialog v-model="dialogLoding" width="150" persistent content-class="PersonalLoading">
         <v-card>
            <v-card-text class="text-xs-center">
               <v-progress-circular
                  indeterminate
                  color="primary"
                  :size="70"
                  :width="7"
               ></v-progress-circular>
            </v-card-text>
         </v-card>
      </v-dialog>
   </div>
</template>

<script>
   import { ContentLoader } from "vue-content-loader"
   import moment from 'moment'
   export default {
      components:{
         ContentLoader
      },
      data() {
         return {
            dialogLoding: false,
            myData :null,
            StudentName:'',
            DateSession:'',
            StartTimes:'',
            EndTimes:'',
            StudentJoin:'',
            CoachJoin:'',
            UserName: '',
            AppoimenID:'',
            CoachNote: ''
         }
      },
      mounted () {
        setTimeout(() => {
          this.myData = 'Example Data';
        }, 2000)

      },
      computed: {
         StoreDataUpcoming(){
            return this.$store.state.LiveSessionSummary.data
         },

         DataUpcoming () {
            const items = []
            const dataValue = this.StoreDataUpcoming.new_appointment

            dataValue.forEach(function (value, key) {
               moment(value.date).format('MMMM, Do YYYY')
               items.push(dataValue);
               // console.log(key +" " + value.date+ " " + value.start_time);
            });

            function compare(a, b){
               if (a.date < b.date) return -1;
               if (a.date > b.date) return 1;
               if (a.start_time < b.start_time) return -1;
               if (a.start_time > b.start_time) return 1;
            }
            return [...dataValue].sort(compare)
         },

         GET_DataUpcoming(){
            return this.DataUpcoming[0]
         },

         GetCoachNote(){
            return this.$store.state.CoachNote
         }

      },
      created() {
         this.$store.dispatch("GET_DASH_UPCOMING_SESSIONS");
         this.StudentName = this.GET_DataUpcoming.student_name
         this.DateSession = this.GET_DataUpcoming.date
         this.StartTimes = this.GET_DataUpcoming.start_time
         this.EndTimes = this.GET_DataUpcoming.end_time
         this.StudentJoin = this.GET_DataUpcoming.student_attend
         this.CoachJoin = this.GET_DataUpcoming.coach_attend
         this.UserName = this.GET_DataUpcoming.username
         this.AppoimenID = this.GET_DataUpcoming.appointment_id
         this.CoachNote = this.GetCoachNote.CoachNote
      },
      filters: {
         dateFormat(value) {
            if (value) {
               return moment(String(value)).format('MMMM, Do YYYY')
            }
         },
      },
      methods: {
         PostCoachSummary() {
            const SummaryData = {
               username : this.UserName,
               appointment_id: this.AppoimenID,
               coach_notes: this.CoachNote
            }
            console.log(SummaryData)
            this.dialogLoding = true
            setTimeout(() => {
               this.$store.dispatch('POST_SESSION_SUMMARY_COACH',SummaryData)
               this.dialogLoding = false
            }, 2000)
         }
      },
      beforeDestroy(){
         this.$store.dispatch("GET_DASH_UPCOMING_SESSIONS");
         this.$store.commit('MuttClearStateAfterLiveSessionAgora')
      },
      destroyed(){
         window.location.reload()
      }
   }
</script>

<style lang="scss">
  @import './SessionSummary.scss';
</style>


